  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.title-small {
  letter-spacing: .5rem;
  font-size: 1.7rem;
}

.title-color {
  color: var(--primary);
}

.grid-container {
  min-height: 100vh;
  grid: "picture header"
        "aside main" 1fr
        "footer footer"
        / 350px auto;
  display: grid;
}

header {
  background: linear-gradient(to right, var(--primary), var(--light));
  text-align: center;
  color: #fff;
  flex-direction: column;
  grid-area: header;
  justify-content: center;
  align-items: center;
  padding: 10rem 5rem;
  display: flex;
}

header h1 {
  letter-spacing: 3rem;
  font-size: 8rem;
  font-weight: 300;
  line-height: 8rem;
}

header p {
  letter-spacing: .5rem;
  font-size: 4rem;
  font-weight: 300;
  line-height: 8rem;
}

.picture {
  background: var(--background);
  border-right: 1px solid var(--border);
  grid-area: picture;
}

aside {
  background: var(--background);
  border-right: 1px solid var(--border);
  grid-area: aside;
  padding: 5rem;
}

aside .contact-block {
  margin-bottom: 10rem;
}

aside .contact-block h3 {
  margin-bottom: 0;
  font-size: 1.4rem;
}

aside .contact-block p {
  margin: 0 0 2rem;
  font-style: italic;
}

aside .contact-block .contact-block-details .github {
  align-items: baseline;
  display: flex;
}

aside .contact-block .contact-block-details .github .p {
  margin-left: 1rem;
}

aside .contact-block .contact-block-details .linkedin {
  align-items: baseline;
  display: flex;
}

main {
  grid-area: main;
  padding: 5rem;
}

main .slogan {
  font-family: var(--font-family2);
  justify-content: center;
  display: flex;
}

main .slogan h2 {
  color: var(--text);
  font-size: 5rem;
}

main .slogan i {
  font-size: 3rem;
}

main h2 {
  margin-top: 4rem;
  margin-bottom: 4rem;
}

main p {
  letter-spacing: .1rem;
  margin-bottom: 3rem;
  line-height: 3rem;
}

main .experience {
  display: flex;
}

