}

main .experience .timeline p {
  margin: 0;
}

main .experience .content {
  padding-left: 2rem;
}

footer {
  text-align: center;
  color: #fff;
  background: linear-gradient(to right, var(--primary), var(--light));
  flex-direction: column;
  grid-area: footer;
  justify-content: center;
  align-items: center;
  padding: 5rem;
  display: flex;
}

footer h2 {
  margin-bottom: 3rem;
}

.button {
  height: 40px;
  width: 40px;
  background: #fff;
  border-radius: 50px;
  margin: 0 5px;
  transition: all .3s ease-out;
  overflow: hidden;
  box-shadow: 0 10px 10px #0000001a;
}

.button .icon {
  height: 40px;
  width: 40px;
  text-align: center;
  box-sizing: border-box;
  border-radius: 50px;
  line-height: 40px;
}

.button .icon i {
  font-size: 25px;
  line-height: 40px;
  transition: all .3s ease-out;
}

