@use "base" as *;
@use "reset" as *;
@use "classes" as *;

@import url('https://fonts.googleapis.com/css?family=Poppins:400,500,600,700&display=swap');


.grid-container {
    display: grid;
    min-height: 100vh;
    grid:
        "picture header" auto "aside main" 1fr "footer footer" auto / 350px auto;
}

header {
    grid-area: header;
    background: linear-gradient(to right, var(--primary), var(--light));
    padding: 10rem 5rem;
    text-align: center;
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    h1 {
        font-size: 8rem;
        line-height: 8rem;
        font-weight: 300;
        letter-spacing: 3rem;
    }

    p {
        font-size: 4rem;
        line-height: 8rem;
        font-weight: 300;
        letter-spacing: 0.5rem;
    }
}

.picture {
    grid-area: picture;
    background: var(--background);
    border-right: 1px solid var(--border);
}


aside {
    grid-area: aside;
    background: var(--background);
    border-right: 1px solid var(--border);
    padding: 5rem;

    .contact-block {
        margin-bottom: 10rem;

        h3 {
            margin-bottom: 0;
            font-size: 1.4rem;
        }

        p {
            margin: 0 0 2rem 0;
            font-style: italic;
        }

        .contact-block-details .github {
            display: flex;
            align-items: baseline;

            .p {
                margin-left: 1rem;
            }


        }

        .contact-block-details .linkedin {
            display: flex;
            align-items: baseline;


        }
    }

}


main {
    grid-area: main;
    padding: 5rem;



    .slogan {
        display: flex;
        justify-content: center;
        font-family: var(--font-family2);

        h2 {
            font-size: 5rem;
            color: var(--text);
        }

        i {
            font-size: 3rem;
        }

    }


    h2 {
        margin-top: 4rem;
        margin-bottom: 4rem;
    }

    p {
        letter-spacing: 0.1rem;
        line-height: 3rem;
        margin-bottom: 3rem;
    }

    .experience {
        display: flex;


        .timeline {
            text-align: center;
            color: var(--hint);
            padding-right: 2rem;
            border-right: 1px solid var(--border);

            p {
                margin: 0;
            }
        }

        .content {
            padding-left: 2rem;
        }
    }



}

footer {
    grid-area: footer;
    display: flex;
    flex-direction: column;
    text-align: center;
    justify-content: center;
    align-items: center;
    color: white;
    padding: 5rem;
    background: linear-gradient(to right, var(--primary), var(--light));

    h2 {
        margin-bottom: 3rem;
    }




}

.button {
    height: 40px;
    width: 40px;
    margin: 0 5px;
    overflow: hidden;
    background: #fff;
    border-radius: 50px;
    box-shadow: 0px 10px 10px rgba(0, 0, 0, 0.1);
    transition: all 0.3s ease-out;
}

.button .icon {
    height: 40px;
    width: 40px;
    text-align: center;
    border-radius: 50px;
    box-sizing: border-box;
    line-height: 40px;
}


.button .icon i {
    font-size: 25px;
    line-height: 40px;
    transition: all 0.3s ease-out;
}